:root {
  font-size: 0.82353em;
  --green: #63D1BB;
  --black: #000000;
  --gray: #ECECEC;
  --white: #fff;
}
@media (min-width: 768px) {
  :root {
    font-size: 0.884em;
  }
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
.container {
  width: auto;
  padding: 0 2.14285714em;
}
@media (min-width: 768px) {
  .container {
    padding: 0 3.33333333em;
  }
}
@media (min-width: 1150px) {
  .container {
    padding: 0;
    max-width: 1052px;
    margin: 0 auto;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-body {
  font-family: "Open Sans", Arial, sans-serif;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
  grid-template-areas: "header" "main" "footer";
}
.page-body__header {
  background-color: var(--white);
  grid-area: header;
}
.page-body__main {
  grid-area: main;
}
.page-body__footer {
  grid-area: footer;
}
.main-header {
  position: relative;
  background-color: var(--white);
}
@media (min-width: 768px) {
  .main-header::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: var(--gray);
    bottom: 0;
  }
}
.menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-size: 1.21428571rem;
}
@media (min-width: 768px) {
  .menu {
    font-size: 100%;
  }
}
.menu__item {
  border-bottom: 1px solid var(--gray);
}
@media (min-width: 768px) {
  .menu__item {
    border-bottom: none;
  }
}
.menu__link {
  text-align: center;
  display: block;
  text-decoration: none;
  color: var(--black);
  padding: 2.07142857rem 0 1.92857143rem 0;
  font-weight: bold;
}
@media (min-width: 768px) {
  .menu__link {
    text-align: start;
    padding: 0;
  }
  .menu__link--end {
    text-align: end;
  }
}
.navigation__info {
  display: none;
}
@media (min-width: 768px) {
  .navigation {
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr auto;
    grid-template-areas: "logo user" "main parag";
    align-items: center;
  }
  .navigation__logo-burger {
    grid-area: logo;
  }
  .navigation__main-menu {
    grid-area: main;
    position: absolute;
    top: 20px;
  }
  .navigation__user-menu {
    grid-area: user;
  }
  .navigation__info {
    grid-area: parag;
    display: block;
    margin: 0;
    position: absolute;
    top: 20px;
    right: 0;
    margin-left: auto;
  }
}
@media (min-width: 768px) and (min-width: 1150px) {
  .navigation {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr auto;
    grid-template-areas: "main logo user" "events . parag";
  }
  .navigation__events-menu {
    grid-area: events;
    position: absolute;
    top: 20px;
  }
  .navigation__main-menu {
    position: static;
  }
}
@media (min-width: 768px) {
  .main-menu {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
  }
  .main-menu__item {
    flex-grow: 1;
  }
}
@media (min-width: 768px) {
  .main-menu {
    justify-content: space-between;
  }
  .main-menu__item {
    flex-grow: 0;
  }
}
@media (min-width: 768px) {
  .user-menu {
    display: grid;
    grid-template-columns: 1fr auto;
    align-items: center;
  }
  .user-menu__item {
    flex-grow: 1;
  }
  .user-menu__span {
    display: none;
  }
  .user-menu__search {
    position: relative;
    height: 88px;
    border-left: 2px solid var(--gray);
    border-right: 2px solid var(--gray);
  }
  .user-menu__icon-search {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .user-menu__link-basket {
    padding: 35px 0;
  }
  .user-menu__basket {
    margin-right: 31px;
    margin-left: 33px;
  }
}
.events-menu {
  display: none;
}
@media (min-width: 1150px) {
  .events-menu {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    column-gap: 65px;
  }
}
.logo-burger {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 1.57142857em 0;
  position: relative;
  border-bottom: 1px solid var(--gray);
}
@media (min-width: 768px) {
  .logo-burger {
    display: block;
    padding: 1.46666667em 0 1.33333333em 0;
    border-bottom: none;
  }
}
@media (min-width: 1150px) {
  .logo-burger {
    padding: 0;
  }
}
.logo-burger__button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}
.logo-burger__button::before {
  content: url("../img/icons/menu-open.svg");
  display: block;
  width: 20px;
  height: 14px;
}
@media (min-width: 768px) {
  .logo-burger__button {
    display: none;
  }
}
.logo-burger__main-link {
  display: block;
  width: 86px;
  height: 35px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .logo-burger__main-link {
    margin: 0;
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .logo-burger__main-link {
    width: 149px;
    height: 72px;
    margin: 0 auto;
  }
}
